import React from 'react';

function index(props) {
    return (
        <div style={{display: "none"}}>
            <div className="sitesearch" itemScope itemType="https://schema.org/WebSite">
                <meta itemProp="url" content="https://www.mumara.com/"/>
                <form itemProp="potentialAction" itemScope itemType="https://schema.org/SearchAction">
                <meta itemProp="target" content="https://www.mumara.com/?s={s}"/>
                <input itemProp="query-input" type="text" name="s" required/>
                <input type="submit"/>
                </form>
            </div>
            <span className="socialorg" itemScope itemType="https://schema.org/Organization">
                <link itemProp="url" href="https://www.mumara.com" />
                <a itemProp="sameAs" href="https://www.facebook.com/MumaraApps">FB</a>
                <a itemProp="sameAs" href="https://twitter.com/MumaraApps">Twitter</a>
                <a itemProp="sameAs" href="https://plus.google.com/+MumaraEmailMarketer">Google+</a>
                <a itemProp="sameAs" href="https://www.youtube.com/channel/UC1k6voQOVss0_PWg7CfJTdw">Youtube</a>
                <a itemProp="sameAs" href="https://www.linkedin.com/company/mumara">Linkedin</a>
            </span>
        </div>
    );
}

export default index;